import React from 'react';
import Layout from '../Layout/Layout';
import Breadcrumbs from '../Breadcrumbs/Breadcrumbs';
import classes from './Blog.module.scss';
import Img from 'gatsby-image';
import { graphql, navigate } from 'gatsby';
import dateimg from '../../img/date.png';
import { documentToReactComponents } from '@contentful/rich-text-react-renderer';

export const query = graphql`
	query($blogTitle: String!) {
		contentfulBlog(title: { content: { elemMatch: { content: { elemMatch: { value: { eq: $blogTitle } } } } } }) {
			title {
				content {
					content {
						value
					}
				}
			}
			date {
				content {
					content {
						value
					}
				}
			}
			img {
				fluid {
					...GatsbyContentfulFluid
				}
			}
			desc {
				json
			}
			category
		}
	}
`;

function Blog({ data }) {
	const title = data.contentfulBlog.title.content[0].content[0].value;
	const date = data.contentfulBlog.date.content[0].content[0].value;
	const img = data.contentfulBlog.img.fluid;
	const cat = data.contentfulBlog.category;
	const options = {
		renderNode: {
			'embedded-asset-block': (node) => {
				const alt = node.data.target.fields.file['en-US'];
				const url = node.data.target.fields.file['en-US'].url;
				return (
					<div className="d-flex justify-content-center pb-2 pt-2">
						<img className="img-fluid w-50" src={url} alt={alt} />
					</div>
				);
			}
		}
	};

	return (
		<Layout title={title}>
			<Breadcrumbs textone="جدید ترین مطالب" linkone="/blog" texttwo={cat} linktwo={`/blog/${cat}`} />

			<div className={`container mt-1 ${classes.cont}`}>
				<h3 dir="rtl" className="text-right pt-3 mr-1">
					{title}
				</h3>
				<p dir="rtl" style={{ color: '#9e9e9e' }} className="text-right">
					<img className="mr-1 pl-1" alt="dateimg" src={dateimg} style={{ width: '23px' }} /> {date}
				</p>
				<div className="d-flex justify-content-center">
					<Img fluid={img} className={`img-fluid ${classes.img}`} />
				</div>
				<div dir="rtl" style={{ fontSize: '1.35em' }} className="text-right mt-4 pb-3">
					{documentToReactComponents(data.contentfulBlog.desc.json, options)}
				</div>
				<div className="d-flex justify-content-end align-items-center pb-3">
					<button onClick={() => navigate(`/blog/${cat}`)} className={`p-1 pl-3 pr-3 ${classes.cat}`}>
						{cat}
					</button>
					<span className="pl-2">: دسته بندی</span>
				</div>
			</div>
		</Layout>
	);
}

export default Blog;
